import Data from "@/js/core/data";
import ENV from "@/js/core/env.js";

const Organization_Details_Server = ENV.get_usersurl() + `/auth/v1/organization-more-data/`;
class OrganizationDetails {
  // Get domain details
  async getOrgInfo(domainId) {
    // eslint-disable-next-line no-unused-vars
    let url = eval("`" + Organization_Details_Server + "`");
    this.url = `${url}${domainId}`;

    let data = new Data(this.url);
    data.disablePCors();
    let result = await data.get();
    return result;
  }
}
export default new OrganizationDetails();
