import Data from "@/js/core/data";

const Shortcut_Server = "https://api.500apps.com/shortcuts/shortcut";
const Shortcut_Region_Server = "https://api.${region}.500apps.com/shortcuts/shortcut";

class ShortcutUrl extends Data {
  constructor() {
    super(Shortcut_Region_Server);
    this.disablePCors();
  }

  // Get shortcut data
  async getShortcutData(name) {
    let url;
    if (window.region && (window.region == "dev" || window.region == "qa")) url = eval("`" + ` ${Shortcut_Region_Server}/${name}` + "`");
    else url = eval("`" + ` ${Shortcut_Server}/${name}` + "`");
    let data = new Data(url);
    data.disablePCors();
    return data.get();
  }
}

export default new ShortcutUrl();
