<template>
  <div>
    <b-container class="px-3">
      <b-row>
        <div class="align-items-center d-flex mx-auto vh-50" v-if="loading">
          <b-spinner variant="primary" label="Spinning" class="d-block mx-auto"></b-spinner>
        </div>
        <b-col md="12" v-if="!loading" class="mx-auto px-0">
          <!-- Display assessment details -->
          <div class="application-header mt-md-2 mt-0">
            <b-row class="p-md-3 px-3 mb-3 mb-md-4">
              <b-col cols="8" md="11" class="pl-3">
                <div class="info-container">
                  <p class="mb-2">{{ test_details.name }}</p>
                  <div class="description custom">
                    <small v-html="test_details.description"></small>
                  </div>
                </div>
              </b-col>
            </b-row>
          </div>
          <!-- Display assessment details -->
          <div :class="`explanation-container ${disable_fields ? 'disable_fields' : ''}`">
            <b-form ref="form" @submit.prevent="save" class="">
              <b-row class="p-3 p-sm-0">
                <!-- Candidate Form Fields starts here -->

                <!-- Picture and Resume upload block starts here -->
                <b-col md="auto" v-if="'picture_url' in form">
                  <b-form-group class="mb-3 finder-upload candidate-picture" v-if="form.picture_url">
                    <img :src="form.picture_url" class="img-fluid uploaded-picture rounded" />
                  </b-form-group>
                  <b-button
                    class=" p-2 px-3 my-3 candidate-picture"
                    :class="isPictureUploaded ? 'reupload-picture btn-outline-secondary' : 'btn-white'"
                    size="sm"
                    variant="primary"
                    @click="showCameraModal('profile-upload')"
                    ><span v-if="isPictureUploaded" class="fe fe-edit edit-hover-icon font-size-lg"></span
                    ><span v-if="!isPictureUploaded"><img src="/img/default-picture.png" class="img-fluid" /><span class="upload-help-text">Select or Upload picture</span></span>
                  </b-button>

                  <b-modal id="profile-upload" modal-class="upload-filemanager" body-class="bg-soft-light " hide-footer title="Upload Profile" @close="stopCamera()" scrollable>
                    <FileUpload
                      class="upload-profile"
                      :app="getStoragePath"
                      @save_photo="uploadCapturedPhoto"
                      @downloadUrl="uploadPicture"
                      :show_camera="true"
                      extensions=".jpg,.png,.jpeg,.gif,.svg"
                      :maxSize="max_size"
                    />
                  </b-modal>

                  <p class="text-danger mt-2" v-if="show_camera_error_message">Please select camera</p>
                </b-col>
                <!-- Name,email, work work_experience starts here -->
                <b-col :md="'picture_url' in form ? '' : 12" class="mt-4 mt-md-0">
                  <b-row>
                    <b-col :md="'picture_url' in form ? 6 : 4" v-if="'name' in form">
                      <b-form-group label="Name" class="mb-3">
                        <b-form-input
                          name="name"
                          autocomplete="name"
                          placeholder="Please enter name"
                          :required="true"
                          onkeypress="return (event.charCode > 64 && event.charCode < 91) || (event.charCode > 96 && event.charCode < 123) || (event.charCode==32)"
                          type="text"
                          v-model="form.name"
                        />
                      </b-form-group>
                    </b-col>
                    <b-col :md="'picture_url' in form ? 6 : 4" v-if="'email' in form">
                      <b-form-group label="Email">
                        <b-form-input name="email" autocomplete="email" placeholder="Please enter email" :required="true" id="email" type="email" v-model="form.email" />
                      </b-form-group>
                    </b-col>
                    <b-col :md="'picture_url' in form ? 6 : 4" v-if="'work_experience' in form">
                      <b-form-group label="Work Experience (Years)">
                        <b-form-input
                          placeholder="Please enter work experience in years"
                          :required="true"
                          id="work_emperience"
                          min="0"
                          max="50"
                          step="any"
                          type="number"
