                      <p class="small text-muted">Maximum file size: 5 MB, Allowed file extensions: .pdf .doc .docx .ods .txt</p>
                      <p class="text-danger" v-if="show_resume_error_message">Please upload resume</p>
                    </b-col>

                    <!-- cover letter block starts here -->
                    <b-col md="12" v-if="'cover_letter' in form" class="mt-2">
                      <label class="mb-0">Cover Letter </label>
                      <b-form-group class="mb-12">
                        <b-form-textarea placeholder="Please provide cover letter" rows="6" :required="true" type="text" v-model="form.cover_letter" />
                      </b-form-group>
                    </b-col>
                    <!-- cover letter block ends here -->
                  </b-row>
                </b-col>
              </b-row>
              <!-- Candidate form save button starts here -->
              <hr class="mb-3 pb-2 mt-2" />
              <span class="text-danger error-message-text">{{ error_message }}</span>
              <b-col class="text-right px-4 px-md-0">
                <div class="d-flex">
                  <p class="small text-danger mr-2" v-if="test_date">
                    Our records indicate that you have taken the test
                    <VueMomentsAgo
                      prefix=""
                      :key="test_date"
                      :date="
                        moment
                          .utc(test_date)
                          .local()
                          .format('YYYY-MM-DD HH:mm:ss')
                      "
                      lang="en"
                    />
                    on
                    {{
                      moment
                        .utc(test_date)
                        .local()
                        .format("YYYY-MM-DD")
                    }}
                    {{ getTimeZone() }}
                  </p>
                  <b-button variant="primary" class="px-4 py-2 ml-auto left" size="sm" type="submit">
                    Next
                  </b-button>
                </div>
