</template>
<script>
  import FileUpload from "@/components/FileUpload";
  import PositionTest from "@/js/ninjainterview/position-tests";
  import { bus } from "@/js/core/emit-bus";
  import VueMomentsAgo from "@/components/VueMoments.vue";

  export default {
    components: { FileUpload, VueMomentsAgo },
    props: { test_details: Object, domain_id: String, test_id: String },
    data() {
      return {
        is_checked: false,
        form: {},
        max_size: "5MB",
        position_id: "",
        error_message: "",
        isPictureUploaded: false,
        show_resume_error_message: "",
        show_camera_error_message: "",
        loading: false,
        test_date: "",
        disable_fields: false,
      };
    },
    computed: {
      getStoragePath() {
        return `ninjainterview/${this.test_id}/photo`;
      },
    },
    async mounted() {
      // Show loader
      this.loading = true;

      // Get candidate and test details
      JSON.parse(this.test_details.candidate_form).forEach((field) => {
        if (field.is_checked == "1") {
          this.form[field.name] = "";
        }
      });

      // Hide loader
      this.loading = false;
    },
    methods: {
      // Get timezones
      getTimeZone(zoneName = "shortGeneric") {
        // set up formatter
        let formatter = new Intl.DateTimeFormat(undefined, {
          timeZoneName: zoneName,
        });
        // run formatter on current date
        return (
          formatter
            .formatToParts(Date.now())
            // extract the actual value from the formatter, only reliable way i can find to do this
            .find((formatted) => formatted.type === "timeZoneName")["value"]
        );
      },
      showCameraModal(id) {
        this.$bvModal.show(id);
      },

      // Save candidate information
      async save() {
        // Check picture is uploaded or not
        if ("picture_url" in this.form && this.form.picture_url == "") {
          this.show_camera_error_message = true;
          setTimeout(() => {
            this.show_camera_error_message = "";
          }, 1000);
          return;
        }
        // Check resume is uploaded or not
        if ("resume" in this.form && this.form.resume == "") {
          this.show_resume_error_message = true;
          setTimeout(() => {
            this.show_resume_error_message = "";
          }, 1000);
          return;
        }
        this.form.test_id = this.test_id;
        try {
          let response = await PositionTest.addCandidate(this.domain_id, this.test_details.position_id, this.form);
          this.disable_fields = true;
          if (response) {
            this.form.id = response.generated_key;
            if (response.test_date) {
              this.test_date = response.test_date;
              setTimeout(() => {
                this.$emit("setCandidateInfo", this.form, "instruction", 1);
                this.disable_fields = false;
              }, 5000);
            } else this.$emit("setCandidateInfo", this.form, "instruction", 1);
          }
        } catch (err) {
          if (err.response.data.error) this.error_message = err.response.data.error;
          setTimeout(() => {
            this.error_message = "";
          }, 2000);
        }
      },

      // Upload the picture
      uploadPicture(url) {
        this.form.picture_url = url;
        this.form = JSON.parse(JSON.stringify(this.form));
        this.isPictureUploaded = true;
        this.$bvModal.hide("profile-upload");
      },

      uploadResume(url) {
        this.form.resume = encodeURI(url);
      },
      // Upload profile pic after capturing the photo
      uploadCapturedPhoto(url) {
        this.form.picture_url = url;
        this.form = JSON.parse(JSON.stringify(this.form));
        this.isPictureUploaded = true;
        this.$bvModal.hide("profile-upload");
      },
      stopCamera() {
        bus.$emit("stop_camera");
      },
    },
  };
</script>
