<template>
  <div>
    <b-container class="mt-0 px-0 px-md-3 mt-md-4 ninja-interview">
      <b-card class="mt-2" no-body>
        <b-card-body class="px-md-5 pt-3 mobile-scroll">
          <b-col cols="3" md="1" class="pl-1 mt-4 mb-2" v-if="org_info.logo_url">
            <div class="company-logo">
              <img :src="org_info.logo_url" />
            </div>
          </b-col>
          <h2 class="mb-2 mt-4" v-else-if="org_info.name">{{ org_info.name }}</h2>
          <h2 class="mb-2 mt-4" v-else>NinjaInterview</h2>
          <!--Showing error messages for safari -->
          <b-row v-if="browser_message">
            <div class="border mx-auto my-7 p-6 rounded text-center border-dashed">
              <div class="mx-7">
                <h1 class="display-2 fe fe-info font-weight-light font-weight-normal mb-1 mb-4 text-gray-700"></h1>
                <h1 class="mb-3 display-4">Browser not supported</h1>
                <p class="mb-2">Please use the latest version of Safari or Microsoft Edge to take the assessment</p>
                <p>
                  <b>Link:</b> {{ assessment_url }}
                  <span class="ml-2">
                    <b-link v-on:click="copyLink" v-b-tooltip.hover class="option-icons" :title="copy_message ? 'Copied the link successfully' : 'Click here to copy'">
                      <i :class="`${copy_message ? 'fe-check text-success' : 'fe-copy'} fe fe-lg copy-icon cursor-pointer`" id="copy" />
                    </b-link>
                  </span>
                </p>
              </div>
            </div>
          </b-row>
          <!-- Position tabs-->
          <b-row class="pt-2" v-else>
            <template v-if="submission_message">
              <div class="d-flex justify-content-center w-100">
                <PostSubmission :message="message" />
              </div>
            </template>
            <template v-else>
              <b-col>
                <!-- All tabs starts here -->
                <b-tabs v-model="tab_index" v-if="!loading" content-class="px-0 pt-3 pb-0">
                  <!-- Candidate Profile tab starts here -->
                  <b-tab active :disabled="disable_tabs.candidate" lazy>
                    <template #title
                      ><div class="d-flex align-items-center"><span class="fe fe-user mr-2"></span><span>Candidate Information</span></div></template
                    >
                    <CandidateProfile @setCandidateInfo="setCandidateInfo" v-bind="{ test_details, domain_id, test_id }" />
                  </b-tab>
                  <!-- Candidate Profile tab ends here -->
                  <!--Instructions tab starts here -->
                  <b-tab :disabled="disable_tabs.instruction" lazy>
                    <template #title
                      ><div class="d-flex align-items-center"><span class="fe fe-list mr-2"></span><span>Instructions</span></div></template
                    >
                    <TestInstructions v-bind="{ test_details, domain_id, test_id, categories, candidate_details, browser_type }" @next="activeNextTab"
                  /></b-tab>
                  <!--Instructions tab ends here -->

                  <!--Test layout tab starts here -->
                  <b-tab :disabled="disable_tabs.layout" lazy
                    ><template #title
                      ><div class="d-flex align-items-center"><span class="fe fe-layers mr-2"></span><span>Questions</span></div></template
                    >
                    <Test :test_details="test_details" :categories="categories" :candidate_details="candidate_details" :browser_type="browser_type" />
                  </b-tab>
                  <!--Test layout tab ends here -->
                </b-tabs>
                <!-- All tabs ends here -->
                <b-spinner v-if="loading" variant="primary" label="Spinning" class="d-block mx-auto"></b-spinner>
              </b-col>
            </template>
          </b-row>
        </b-card-body>
        <div class="border-top ninjainterview-footer p-2 text-right ">
          <div class="small pr-4 my-1">
            <span class="">Powered by</span><a target="_blank" class="text-primary ml-1" href="http://500apps.com/"><img src="/img/favicon.webp" style="width: 17px;" class="ml-2" /> 500apps</a>
          </div>
        </div>
      </b-card>
    </b-container>
  </div>
</template>
<script>
  import CandidateProfile from "./CandidateProfile";
  import TestInstructions from "./TestInstructions";
  import Test from "./Test";
  import PositionTest from "@/js/ninjainterview/position-tests";
  import PostSubmission from "./PostSubmission";
  import { bus } from "@/js/core/emit-bus";
  import Client from "@/js/core/client";
  import CopyToClipboard from "@/js/core/copy-to-clipboard";
  import ShortcutUrl from "@/js/ninjainterview/shortcuturl";
  import OrganizationDetails from "@/js/ninjainterview/organization-details";
  export default {
    name: "Home",
    components: { CandidateProfile, Test, TestInstructions, PostSubmission },
    data() {
      return {
        tab_index: 0,
        domain_id: window.domain,
        test_id: "",
        testData: [],
        test_details: {},
        categories: [],
        loading: true,
        candidate_details: {},
        disable_tabs: {
          candidate: false,
          instruction: true,
          layout: true,
        },
        submission_message: false,
        message: "Thanks for completing the assessment.",
        assessment_url: "",
        browser_message: false,
        copy_message: false,
        browser_type: Client.getVisitorInfo(),
        org_info: [],
      };
    },
    async mounted() {
      let shortcutData;
      let host = window.location.host;
      if (host.includes(".ninjainterview.com")) {
        shortcutData = await ShortcutUrl.getShortcutData(host.split(".ninjainterview.com")[0]);
        if (!shortcutData || !shortcutData.length) return;
        window.domain = shortcutData[0].domain_id;
        // Set position_id and test_id if the shortcut url type is test level
        if (shortcutData[0].link == "test_level") {
          if (shortcutData[0].data_1) {
            window.position_id = JSON.parse(shortcutData[0].data_1).test_type.split("-")[0];
            window.test_id = JSON.parse(shortcutData[0].data_1).test_type.split("-")[1];
          }
        }
        this.domain_id = window.domain;
        window.region = shortcutData[0].env;
      }
      // Assessment URL from location
      this.assessment_url = window.location.href;
      console.log("Client.getVisitorInfo()", this.browser_type);
      console.log("OS", this.browser_type.OS);
      console.log("Browser", this.browser_type.browser);
      // Check browser type and show message for iPhone Chrome browser
      if (this.browser_type.OS == "iPhone/iPod" && this.browser_type.browser == "Chrome") {
        this.browser_message = true;
        return;
      }

      bus.$on("showSubmissinMessage", (type) => {
        if (type == "regular") this.submission_message = true;
      });
      this.test_id = this.$route.params.test_id ? this.$route.params.test_id : window.test_id;
      this.getTestData();
      // Get organization details
      this.getOrganizationDetails();
    },
    methods: {
      // Get organization details
      async getOrganizationDetails() {
        this.org_info = await OrganizationDetails.getOrgInfo(this.domain_id);
      },
      // Get test info and questions
      async getTestData() {
        let resp = await PositionTest.getTest(this.domain_id, this.test_id);
        // Test details
        if (resp.test_details.length) this.test_details = resp.test_details[0];

        // Check categories
        if (resp.categories.length) {
          // Get category_ids
          let category_ids = resp.categories.map((category) => category.category_id);

          // Get unique category id's
          category_ids = [...new Set(category_ids)];

          // Get questions based on category id
          category_ids.map((category_id) => {
            let question = resp.categories.filter((res) => res.category_id == category_id);
            this.categories.push({ category_id, name: question[0].name, description: question[0].description, questions: question });
          });
        }
        this.loading = false;
      },
      //Sets candidate details
      setCandidateInfo(form, tab_name, tab_index) {
        this.candidate_details = form;
        this.activeNextTab(tab_name, tab_index);
      },
      // Active current tab and disable other tabs
      activeNextTab(tab_name, tab_index) {
        this.disable_tabs = {
          candidate: true,
          question_count: true,
          instruction: true,
          layout: true,
        };
        this.disable_tabs[tab_name] = false;
        this.tab_index = tab_index;
      },
      // Copy to clipboard.
      copyLink() {
        this.copy_message = true;
        setTimeout(() => {
          this.copy_message = false;
        }, 4000);
        CopyToClipboard.fallbackCopyTextToClipboard(this.assessment_url);
      },
    },
  };
</script>
