<template>
  <span v-if="date" class="vue-moments-ago">{{ prefix }} {{ humanDifference | plural(humanWord, lang) }} {{ suffix }}</span>
</template>

<script>
  export default {
    data() {
      return {
        epochs: ["year", "month", "day", "hour", "minute"],
        epochs_kr: ["", "", "", "", ""],
        epochs_jp: ["", "", "", "", ""],
        year: 31536000,
        month: 2592000,
        day: 86400,
        hour: 3600,
        minute: 60,
        humanReadable: "",
        humanDifference: 0,
        humanWord: "moment",
        timer: {},
      };
    },

    props: {
      prefix: {
        type: String,
        default: "posted",
      },
      suffix: {
        type: String,
        default: "ago",
      },
      date: {
        type: String,
        required: true,
      },
      lang: {
        type: String,
        default: "en",
      },
    },

    mounted() {
      this.getSeconds(this.date);
      this.timer = setInterval(() => {
        this.getSeconds(this.date);
      }, 1000);
    },

    filters: {
      plural(value, name, lang) {
        // eslint-disable-next-line no-unused-vars
        let plural;
        if (value === 0) {
          if (lang == "kr") {
            return "" + name;
          } else if (lang == "jp") {
            return "" + name;
          } else {
            return "a few " + name + "s";
          }
        } else if (value > 1) {
          if (lang == "en") {
            return value + " " + name + "s";
          } else {
            return value + " " + name + "";
          }
        } else {
          return value + " " + name;
        }
      },
    },

    methods: {
      getSeconds(time) {
        let seconds = this.moment().diff(this.moment(time), "seconds");
        this.humanReadable = this.getDuration(seconds);
        if (this.humanReadable) {
          this.humanDifference = this.humanReadable.interval;
          this.humanWord = this.humanReadable.humanEpoch;
        }
      },
      getDuration(seconds) {
        let epoch, interval;
        let humanEpoch;
        for (let i = 0; i < this.epochs.length; i++) {
          epoch = this.epochs[i];
          if (this.lang == "kr") {
            humanEpoch = this.epochs_kr[i];
          } else if (this.lang == "jp") {
            humanEpoch = this.epochs_jp[i];
          } else {
            humanEpoch = this.epochs[i];
          }
          interval = Math.floor(seconds / this[epoch]);
          if (interval >= 1) {
            return { interval: interval, humanEpoch: humanEpoch };
          }
        }
      },
    },
    destroyed() {
      clearInterval(this.timer);
    },
  };
</script>

<style scoped>
  .vue-moments-ago {
    font-size: 12px;
  }
</style>
